import React from 'react';
import PlayerName from './PlayerName';
import {translateTeam} from '../../helper';

const PlayerLabels = props => {
  const {player, height, annotations, isMobile, step, lang} = props;
  return (
    <div
      className="labels-container"
      key={`player-${player.id}`}
      style={{
        height: `${height}px`
      }}
    >
      {player.teamsForDots.length > 0 && (
        <PlayerName {...{player,isMobile}}/>
      )}
      {player.teamsForDots
        //.filter((d,i) => d.join !== 'unbekannt' && (d.left !== 'unbekannt' && i > 0))
        .map(d => {
          //drawPlayer(player, true, {x: this.xScale, y: this.yScale})
          const x = d.x; // this.svgXScale(d.year);
          const y = d.y; // this.svgYScale(d.teams);

          const labelStyle = {
            left: `${x}px`,
            top: `${y}px`
          };
          let annotated = '';
          if (annotations.length) {
            const annotation = annotations.find(
              annotation => annotation.y === d.index
            );
            annotated = `annotations ${
              annotation ? 'annotated' : ''
            }`;
          }
          return (
            <div
              key={`${player.id}-${d.year}-${d.date ||
                'no-date'}-${d.join ||
                'no-join'}-${d.left || 'no-left'}`}
              className={`team-label ${annotated}`}
              style={labelStyle}
            >
              {!d.missingYear &&
                ((d.join !== 'karriereende' && !isMobile) ||
                  isMobile) &&
                d.join !== 'vereinslos' &&
                d.join !== 'active' && (
                  <span className="team-name">
                    {translateTeam(d.teamName || d.team, lang)}
                    {d.note &&
                      d.note !== '-' && (
                        <i className="note">
                          Stagione {d.season}
                          <br />
                          {d.note.split(';')[0]}
                        </i>
                      )}
                  </span>
                )}

              <span className="team-years">
                {!isMobile &&
                  (player.teamYears || []).map(team => {
                    if (
                      team.key === `${d.team}-${d.teams}` &&
                      team.key.search(/karriereende/gi) &&
                      d.year === team.value.minYear &&
                      d.left
                    ) {
                      return (
                        <b
                          key={`${team.value.minYear}-${
                            team.value.maxYear
                          }`}
                          style={{
                            marginLeft: `${team.value
                              .years * step}px`
                          }}
                        >
                          {team.value.maxYear -
                            team.value.minYear ===
                          0
                            ? `${team.value.minYear}`
                            : `${team.value.minYear}-${
                                team.value.maxYear
                              }`}
                        </b>
                      );
                    }
                    return '';
                  })}
              </span>
            </div>
          );
        })}
    </div>
  );
};

export default PlayerLabels;



