import React, { Component } from "react";
import DOMPurify from "dompurify";
import PlayersDataViz from "./components/PlayersDataViz/PlayersDataViz";
import Distributions from "./components/Distributions/OverlappedDistributions";

import competitionsList from "./components/competitions.json";

import CompetitionsComparison from "./components/CompetitionsComparison/CompetitionsComparison";
import TeamsComparison from "./components/TeamsComparison/TeamsComparison";
import OneClubPlayers from "./components/PlayersComparison/OneClubPlayers";
import Journeymen from "./components/PlayersComparison/Journeymen";
import "./App.css";

import translations from "./translations.json";

const PLAYERS_THRESHOLD = 3750;

window.getImage = () => {
  const img = document.createElement("img");
  const canvas = document.getElementsByTagName("canvas")[0];
  img.src = canvas.toDataURL();
  document.body.appendChild(img);
};

class App extends Component {
  state = {
    lang: "en"
  };
  componentDidMount() {
    this.setState({
      lang: document.querySelector("html").getAttribute("lang")
    });
  }
  sendAnalytics(player, event = "search-player") {
    if (!player) {
      return;
    }
    // console.log('sendAnalytics',event,player)
    if (typeof window.gtag !== "undefined") {
      window.gtag("event", event, {
        event_category: "engagement",
        event_label: player
      });
    }
  }
  render() {
    const { lang } = this.state;
    return (
      <div className="content-wrapper essay">
        <div className="container-fluid">
          {/*<Header />*/}
          <div className="row no-margin-row">
            <div className="col-xs-12">
              <section>
                <PlayersDataViz
                  file="prefetchedPlayers.json"
                  lang={lang}
                  analyticsCallback={player => this.sendAnalytics(player)}
                />
              </section>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-6 col-sm-offset-3">
              <section>
                {lang === "it" && <h2>I campionati</h2>}
                {lang === "it" && <p>
                  &Egrave; La Liga spagnola il campionato in cui un
                  giocatore passa pi&ugrave; tempo nello stesso club, quasi quattro
                  anni in media (3.87 anni). La Serie A si posiziona fuori dai primi venti
                  campionati, con periodi di permanenza
                  pi&ugrave; brevi (2.89 anni in media), tuttavia in serie A hanno giocato bandiere,
                  come Paolo Maldini, Francesco Totti o Alessandro Costacurta con pi&ugrave; di 20
                  anni passati nella stessa squadra.
                </p>}
                {lang === "en" && <h2>Leagues</h2>}
                {lang === "en" && <p>
                The spanish La Liga is the league in which players tend to be more loyal to their teams (3.87 years on average). The Premier League and the German Bundesliga are also showing more than three years on average. The Italian Serie A is off the chart. However in Italy we recorded the most loyal players like Francesco Totti and Paolo Maldini (25 years).
                </p>}
                <CompetitionsComparison
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                  lang={lang}
                />
                {lang === 'en' && <div className="chart-notes">The chart above shows leagues for which we have at least 2,000 players</div>}
                {lang === 'it' && <div className="chart-notes">Nel grafico vengono inclusi solamente in campionati per cui abbiamo 2000 giocatori</div>}
              </section>
              <section>
                {lang === "it" && <h2>Le squadre</h2>}
                {lang === "it" && (
                  <p>
                    Non tutti i giocatori diventano bandiere della squadra in
                    cui giocano, casi come quelli di Totti o Maldini sono rari,
                    l'analisi approfondita delle carriere e dell'appartenenza ad
                    una squadra ci racconta una storia diversa.
                    <br />
                    Ci sono squadre per cui il senso di attaccamento &egrave;
                    maggiore e i giocatori tendono a rimanervi per pi&ugrave;
                    anni.
                    <br />
                    Athletic Bilbao e Real Sociedad, grazie alla regola per cui
                    solamente giocatori baschi possono giocare nelle loro fila
                    (per la Real Sociedad solo fino al 1989) e il grande
                    orgoglio nazionalista, registrano il numero anni di
                    permanenza medio pi&ugrave; alto.
                  </p>
                )}
                {lang === "en" && <h2>Teams loyalty</h2>}
                {lang === "en" && (
                  <p>
                  Football is changing fast. Famous and rich teams are getting more powerful, while smaller sides are struggling to keep their young players, dazzled by big money and dreams of glory.
                  <br/>
                  However, there are teams in which loyalty is still an important factor for their players.
                  <br/>
                  Athletic Bilbao and Real Sociedad, thanks to their signing policy, whereby the club only signs players from the Basque Country (Real Sociedad only till 1989), have promoted local talent and created a sense of belonging that fostered loyalty.
                  </p>
                )}
                <TeamsComparison
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                  lang={lang}
                />
                {lang === 'en' && <div className="chart-notes">The chart above shows teams for which we have at least 150 players</div>}
                {lang === 'it' && <div className="chart-notes">Nel grafico vengono inclusi solamente le squadre per cui abbiamo 150 giocatori</div>}
              </section>
              <section>
                <h2>{translations[lang].flags.h2}</h2>
                <p
                  dangerouslySetInnerHTML={{
                    __html: DOMPurify.sanitize(translations[lang].flags.p)
                  }}
                />
                <OneClubPlayers
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                  lang={lang}
                />
              </section>

              <section>
                <h2>{translations[lang].journeymen.h2}</h2>
                <p
                  dangerouslySetInnerHTML={{
                    __html: DOMPurify.sanitize(translations[lang].journeymen.p)
                  }}
                />
                <Journeymen lang={lang} />
              </section>
              <section>
                {lang === "it" && <h2>Gli effetti della legge Bosman</h2>}
                {lang === "en" && <h2>The Bosman Ruling</h2>}
                {lang === "it" && (
                  <p>
                    La <i>sentenza Bosman</i> ha aperto i confini del calcio nel
                    1995. La vita di tanti giocatori &egrave; cambiata e per la
                    grande maggioranza &egrave; aumentato il numero di maglie
                    vestite durante il corso di una carriera.
                    <br />
                    Questo grafico confronta i periodi prima e dopo la sentenza.
                    La versione del grafico che mostra gli anni dal 1995 ad oggi
                    mostra come la tendenza sia quella di trasferirsi pi&ugrave; spesso
                    in squadre diverse, inoltre la forma pi&ugrave; appiattita indica
                    un grado di variabilit&agrave; maggiore.<br/>
                    Il grafico evidenzia anche la sensibilie diminuzione del numero di <i>one-men club</i> da circa 5% fino a quasi scomparire.
                  </p>
                )}
                {lang === "en" && (
                  <p>
                  The <b>Bosman ruling</b> removed a lot of the limitations on the free transfer of players in Europe.
                  It is one of the reasons football changed, for many players the number of teams they played for and countries they visited increased.
                  <br/>
                  The following chart shows how the number of teams a footballer plays for in their career has shifted toward higher numbers over the past two decades.
                  It also highlights how the percentage of one-club men has almost disappeared after 1995.
                  The charts also flatten out after 1995 in a way that it indicates greater variability in the number of teams players play for in his career.
                  </p>
                )}
                <Distributions
                  file="distributions.json"
                  lang={lang}
                  max={0.2}
                  groupMap={
                    lang === "it"
                      ? {
                          before: {
                            title: "Prima della sentenza",
                            subtitle: "1930-1995"
                          },
                          after: {
                            title: "Dopo la sentenza",
                            subtitle: "1995-2018"
                          },
                        }
                      : {
                          before: {
                            title: "Before the ruling",
                            subtitle: "1930-1995"
                          },
                          after: {
                            title: "After the ruling",
                            subtitle: "1995-2018"
                          },
                        }
                  }
                />
                {lang === 'en' && <div className="chart-notes">The chart above shows data only for players with at least ten years career long</div>}
                {lang === 'it' && <div className="chart-notes">Nel grafico vengono inclusi solamente giocatori con almeno 10 anni di esperienza</div>}
              </section>
              <section>
                {lang === 'it' && <h2>Dati</h2>}
                {lang === 'it' && <p>
                  I dati visualizzati in questa pagina provengono da numerose fonti come Wikipedia, Transfermarkt e altri database online. I dati sono stati prima scaricati, poi incrociati e controllati. Il database &egrave; formato da pi&ugrave; di 100.000 giocatori provenienti da 200 nazioni che hanno giocato in quasi tutti i campionati, dalla Serie A alla I-League indiana, passando per Africa e Sud America.
                  <br/>
                  Questi dati sono il risultato di lunghe ricerche e numerosi test, tuttavia &egrave; possibile incontrare anomalie o errori, sia nei nomi dei giocatori e delle squadre che negli anni rappresentati nelle carriere.
                  <br/>
                  Invitiamo chiunque si imbattesse in errori a comunicarcelo, faremo il nostro meglio per correggere gli errori.
                </p>}
                {lang === 'en' && <h2>Data</h2>}
                {lang === 'en' && <p>
                  The information visualized on this page comes from several sources (Wikipedia, Transfermarkt and other online databases). The data were first downloaded, aggregated and checked several times. The resulting database is made of more than 100,000 football players from 200 countries who played for almost all the leagues in the world, from the English Premier League to the Indian I-League, through Africa and South America.
                  <br/><br/>
                  This data is the result of a long research and tests, however, it is possible to find anomalies and errors, perhaps in the right name of players and teams, or in the years used to build the careers.
                  If you stumble upon an error, please do not hesitate to contact us, we will do our best to fix it.
                </p>}
              </section>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



