import React, { Component } from "react";
import { line, curveLinear as curve } from 'd3-shape';
import "./regression-line.css";

const drawLine = line()
  .x(d => d.x)
  .y(d => d.y)
  .curve(curve);

class RegressionLine extends Component {
  state = {
  }

  render() {
    const {id,data,title,scales,mean}=this.props;

    return (
      <g id={id} className="regression-line">
      <path id={`${id}Path`} d={drawLine(data.points.map(d => ({
        x: scales.x(d[0]),
        y: scales.y(mean),
      })))} className="bg"/>
        <path id={`${id}Path`} d={drawLine(data.points.map(d => ({
          x: scales.x(d[0]),
          y: scales.y(mean),
        })))} />

        <text dy="-10px">
          <textPath href={`#${id}Path`} startOffset="0%">
            {title}
          </textPath>
        </text>
      </g>
    );
  }
}

export default RegressionLine;



