import React, { Component } from "react";
import { extent, max } from "d3-array";
import { nest } from "d3-collection";
import BarChart from "../BarChart/BarChart";

// import competitionsPlayers from "../playersInLeagues.json";
// import teamsInfo from "../teams.json";

import translations from "../../translations.json";

import "./players.css";

const FIELDS = ["max", "avg"];

class Journeymen extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  async componentDidMount() {
    const res = await fetch(`assets/transfers/data/journeymen.json`);
    const stats = await res.json();
    this.setState({
      stats
    });
  }
  render() {
    const { lang } = this.props;
    const { stats } = this.state;

    if (!stats) {
      return null;
    }

    return (
      <div className="players">
          <BarChart
            data={stats.map(d => {
              return {
                key: d.name,
                title: (
                  <span>
                    {d.name}
                    <br />
                    {d.nationalities[0]}
                  </span>
                ),
                numberOfTeams: d.teams
              };
            })}
            fields={["numberOfTeams"]}
            extents={{ numberOfTeams: [0, 32] }}
            label={translations[lang]['transfers']}
          />
      </div>
    );
  }
}

export default Journeymen;



