import React, { Component } from "react";
import { extent } from "d3-array";
import { scaleLinear } from "d3-scale";
import { format } from "d3-format";

import translations from "../../translations.json";

import "./barchart.css";
const numberFormat = (n, label='') => `${format('~f')(+format(',.3n')(n))} ${label}`;

class BarChart extends Component {
  render() {
    const {data,fields,extents,label, lang = 'en'} = this.props;
    // console.log('DATA',data)
    if(!data) {
      return null;
    }

    const xScale = scaleLinear().range([0,100]);
    fields.forEach(field => {
      xScale.domain(extent([...xScale.domain(),...extents[field]]));
    })
    // console.log(xScale.domain())
    return <div className="barchart">
      <ul>
        {data.map((d, barIndex) => {
          return <li key={d.key}>
            <i>{d.title}</i>
            <div className="bars">
              {fields.map(field => {
                const width = xScale(d[field]);
                return <span key={`${field}-${d.title}`} className="bar" style={{width:`${width}%`}}>
                  <b>{numberFormat(d[field])} <i>{label}</i></b>
                  {barIndex === 0  && fields.length > 1 && (
                    <div className="bar-annotation"><span>{translations[lang][`${field}_duration`]}</span></div>
                  )}
                </span>
              })}
            </div>
          </li>
        })}
      </ul>
    </div>
  }
}

export default BarChart;



