import React, { Component } from "react";
import { extent, max } from "d3-array";
import { nest } from "d3-collection";
import BarChart from "../BarChart/BarChart";

import translations from "../../translations.json";

import "./players.css";

const FIELDS = ["max", "avg"];

class OneClubPlayers extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  async componentDidMount() {
    const res = await fetch(`assets/transfers/data/oneclubmen.json`);
    const stats = await res.json();
    this.setState({
      stats,
    });
  }
  render() {
    const { lang } = this.props;
    const { stats } = this.state;

    if (!stats) {
      return null;
    }

    return (
      <div className="players">
          <BarChart
            data={stats.long
              .map(d => {
                d.key = d["_id"].key;
                d.title = (
                  <span>
                    {d["_id"].name}
                    <br />
                    {d.longestTeam.teamName}
                  </span>
                );
                return d;
              })}
            fields={["longest"]}
            extents={{ longest: [0, 25] }}
            label={translations[lang]['years']}
          />
      </div>
    );
  }
}

export default OneClubPlayers;



