import React, { Component } from 'react';
import DOMPurify from 'dompurify';

import './SimpleSlider.css'

class SimpleSlider extends Component {
  state = {
    status: 1,
  }
  updateStatus() {
    const status = +(!this.state.status);
    console.log(status, this.state.status)
    if(this.props.callback) {
      this.props.callback(status);
    }
    this.setState({
      status,
    })
  }
  render() {
    const {status} = this.state;
    const {labels} = this.props;
    const handleStatus = status ? 'right' : 'left';
    return <div className="simple-slider" onMouseDown={() => this.updateStatus()}>
      <div className="slider-container">
        <span className="slider-label">{labels[0]}</span>
        <div className="slider-rail"><span className={`slider-handle ${handleStatus}`}/></div>
        <span className="slider-label">{labels[1]}</span>
      </div>
    </div>
  }
}

export default SimpleSlider;



