const getRetinaRatio = () => {
  const devicePixelRatio = window.devicePixelRatio || 1;
  const c = document.createElement("canvas").getContext("2d");
  const backingStoreRatio = [
    c.webkitBackingStorePixelRatio,
    c.mozBackingStorePixelRatio,
    c.msBackingStorePixelRatio,
    c.oBackingStorePixelRatio,
    c.backingStorePixelRatio,
    1
  ].reduce((a, b) => a || b);

  return devicePixelRatio / backingStoreRatio;
};

const translateNote = note => {
  const translations = {
    "joined from": "Trasferito da",
    "returned after loan spell with": "Fine prestito da",
    "loaned from": "In prestito da",
    "joined in": "Arrivato in",
    "from:": "da:",
    "without club": "Senza squadra",
    "end of loan": "Fine prestito",
    loan: "In prestito",
    "fee:": "per ",
    "free transfer": "Costo zero"
  };
  for (let text in translations) {
    const regex = new RegExp(text, "gi");
    note = note.replace(regex, translations[text]);
  }
  return note;
};

