module.exports = [{"chapter":{"index":0,"en":{"title":"The legend of King Kazu","text":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a> (三浦知良), known as <i>King Kazu</i>, he is a Japanese living legend footballer, holding the records for being the oldest footballer and goalscorer in worldwide professional leagues. He surpassed <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\" data-index=\"0\" data-chapter=\"preface\">Sir Stanley Matthews</a> when he played at 50 years and 7 days during Yokohama vs V-Varen Nagasaki.","shortText":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a>, Japanese superstar footballer, he holds the records for being the oldest footballer and goalscorer in worldwide professional leagues. He surpassed <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\" data-index=\"0\" data-chapter=\"preface\">Sir Stanley Matthews</a> when he played at 50 years and 7 days during Yokohama vs V-Varen Nagasaki."},"it":{"title":"La leggenda di King Kazu","text":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a> (三浦知良), detto <i>King Kazu</i>, leggenda vivente del calcio giapponese e mondiale, detiene il record di giocatore professionista e marcatore pi&ugrave; anziano in attivit&agrave; al mondo. Ha battuto il record detenuto da <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\"  data-index=\"0\"  data-chapter=\"preface\">Sir Stanley Matthews</a>, quando, il 5 Marzo 2017, gioc&ograve; a 50 anni e sette giorni di et&agrave;, la partita tra la sua squadra, i Yokohama FC, e il V-Varen Nagasaki.","shortText":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a>, eroe del calcio giapponese, con il record di giocatore e marcatore pi&ugrave; anziano in attivit&agrave; al mondo, il 5 Marzo 2017 ha battuto il primato di <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\" data-index=\"0\" data-chapter=\"preface\">Sir Stanley Matthews</a>, giocando a 50 anni e sette giorni in Yokohama - V-Varen Nagasaki."}},"id":"Kazuyoshi Miura-Feb 26, 1967","height":840},{"chapter":{"index":1,"en":{"title":"The One-club Men","text":"A one-club man is a footballer who has played his entire professional career with only one club. <a href=\"#\" data=\"Francesco Totti-Sep 27, 1976\" data-index=\"1\">Francesco Totti</a> (25 years, AS Roma), <a href=\"#\" data=\"Paolo Maldini-Jun 26, 1968\" data-index=\"1\">Paolo Maldini</a> (25 years, Milan AC) and <a href=\"#\" data=\"Ryan Giggs-Nov 29, 1973\" data-index=\"1\">Ryan Giggs</a> (24 years, Manchester United) are now part of the history of the clubs where they played. However, the legendary Turkish player <a href=\"#\" data=\"Said Altınordu-Jul 24, 1912\" data-index=\"1\">Said Altınordu</a> holds the record with 27 years playing for the Altınordu S.K.","shortText":"A one-club man is a footballer who has played his entire professional career with only one club: <a href=\"#\" data=\"Francesco Totti-Sep 27, 1976\" data-index=\"1\">Francesco Totti</a> (25 years, AS Roma), <a href=\"#\" data=\"Paolo Maldini-Jun 26, 1968\" data-index=\"1\">Paolo Maldini</a> (25 years, Milan AC) and <a href=\"#\" data=\"Ryan Giggs-Nov 29, 1973\" data-index=\"1\">Ryan Giggs</a> (24 years, Manchester United). However, the Turkish player <a href=\"#\" data=\"Said Altınordu-Jul 24, 1912\" data-index=\"1\">Said Altınordu</a> holds the record with 27 years playing for the Altınordu S.K."},"it":{"title":"I giocatori bandiera","text":"La carriera di un giocatore di calcio dura in media <b>otto</b> anni. Le <i>bandiere</i> come <a href=\"#\" data=\"Francesco Totti-Sep 27, 1976\" data-index=\"1\">Francesco Totti</a> (25 anni, AS Roma), <a href=\"#\" data=\"Paolo Maldini-Jun 26, 1968\" data-index=\"1\">Paolo Maldini</a> (25 anni, Milan AC) e <a href=\"#\" data=\"Ryan Giggs-Nov 29, 1973\" data-index=\"1\">Ryan Giggs</a> (24 anni, Manchester United) sono entrati per sempre nell'olimpo del calcio come <i>one-club man</i> per aver giocato tutta la loro carriera professionistica in una sola squadra. Il record &egrave; detenuto dal turco <a href=\"#\" data=\"Said Altınordu-Jul 24, 1912\" data-index=\"1\">Said Altınordu</a> (27 anni, Altınordu S.K.).","shortText":"Le <i>bandiere</i> come <a href=\"#\" data=\"Francesco Totti-Sep 27, 1976\" data-index=\"1\">Francesco Totti</a> (25 anni, AS Roma), <a href=\"#\" data=\"Paolo Maldini-Jun 26, 1968\" data-index=\"1\">Paolo Maldini</a> (25 anni, Milan AC) e <a href=\"#\" data=\"Ryan Giggs-Nov 29, 1973\" data-index=\"1\">Ryan Giggs</a> (24 anni, Manchester Utd) sono entrati nella storia del calcio come <i>one-club man</i> per aver giocato tutta la loro carriera in una squadra. Il record di 27 anni &egrave; per&ograve; del turco <a href=\"#\" data=\"Said Altınordu-Jul 24, 1912\" data-index=\"1\">Said Altınordu</a> (Altınordu S.K.)."}},"id":"Francesco Totti-Sep 27, 1976","substories":[{"id":"Francesco Totti-Sep 27, 1976","name":"Francesco Totti","type":"short","descr":"<i>Er Pupone</i>, uno dei giocatori più forti di sempre, nel corso della sua carriera professionistica ha sempre militato nella Roma, squadra di cui è stato capitano dal 1998 al 2017, per un totale record di 19 stagioni.","shortDescr":"Nel corso della sua carriera professionistica ha sempre militato nella Roma, squadra di cui è stato capitano dal 1998 al 2017, per un totale record di 19 stagioni, in cui ha giocato 619 partite e segnato 250 goal."},{"id":"Paolo Maldini-Jun 26, 1968","name":"Paolo Maldini","type":"short","descr":"Difensore di statura mondiale, annoverato fra i migliori nella storia del calcio, nel corso della sua carriera, durata 25 anni, ha vestito solo la maglia del Milan, con cui ha vinto 26 trofei: 7 scudetti, 1 Coppa Italia, 5 Supercoppe italiane, 5 Champions League, 5 Supercoppe europee, 2 Coppe Intercontinentali e 1 Coppa del mondo per club FIFA.","shortDescr":"Difensore di statura mondiale, annoverato fra i migliori nella storia del calcio, nel corso della sua carriera, durata 25 anni, ha vestito solo la maglia del Milan, con cui ha vinto 26 trofei."},{"id":"Ryan Giggs-Nov 29, 1973","name":"Ryan Giggs","type":"short","descr":"Dal 1990 al 2014 ha militato nel Manchester United, club di cui è primatista assoluto di presenze (963, di cui 159 nelle competizioni UEFA per club).<br/>Con 37 trofei conquistati, è il giocatore più vincente nella storia del calcio inglese."}]},{"chapter":{"index":2,"en":{"title":"The Journeymen","text":"The term <i>Journeyman</i> refers to a professional footballer who plays for a large number of different clubs during their career. <a href=\"#\" data=\"Sebastián Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a> has been officially recognized by Guinness Book of World Records as the most-traveled man in football. Prime examples of journeyman are also <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> or the Italian <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>. However not properly a journeyman, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a> has played in 7 different leagues during his career.","shortText":"<i>Journeyman</i> are footballers who played for a large number of different clubs.  The Guinness World Record belongs to <a href=\"#\" data=\"Sebastián Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a>. Prime examples of journeyman are also <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> or the Italian <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>."},"it":{"title":"I giramondo del calcio","text":"Sono quei giocatori che cambiano casacca numerose volte durante la loro carriera. In inglese vengono chiamati <i>journeyman</i>. <a href=\"#\" data=\"Sebastián Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a> &egrave; il detentore del Guinnes dei Primati di questa speciale categoria. Tra <i>giramondo</i> meno famosi, come <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> e <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>, troviamo uno dei pi&ugrave; grandi talenti di sempre, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a>, che ha militato in sette campionati diversi.","shortText":"In inglese vengono chiamati <i>journeyman</i>. Il record assoluto di trasferimenti &egrave; detenuto da <a href=\"#\" data=\"Sebastián Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a>. Tra <i>giramondo</i> meno famosi, come <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> e <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>, troviamo uno dei pi&ugrave; grandi talenti di sempre, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a>."}},"id":"Sebastián Abreu-Oct 17, 1976","substories":[{"id":"Zlatan Ibrahimovic-Oct 3, 1981","name":"Zlatan Ibrahimovic","type":"short","descr":"Zlatan ha militato in sette campionati diversi. Considerato uno dei calciatori più forti e completi al mondo, occupa il 9º posto della classifica dei marcatori della UEFA Champions League.<br/>Nella sua carriera ha cambiato nove maglie.","shortDescr":"Ha militato in sette campionati diversi. Considerato uno dei calciatori più forti e completi al mondo, occupa il 9º posto della classifica dei marcatori della UEFA Champions League.<br/>Nella sua carriera ha cambiato nove maglie."},{"id":"Sebastián Abreu-Oct 17, 1976","name":"Sebastián \"Loco\" Abreu","type":"short","descr":"Il cileno <i>Loco</i> Abreu &egrave; &egrave; stato iscritto nel Guinnes dei Primati come il giocatore che ha cambiato pi&ugrave; squadre nella storia del calcio. Ha cambiato casacca ben <b>26</b> volte, visitando 11 nazioni, Uruguay, Argentina, Spagna, Brasile, Messico, Israele, Grecia, Ecuador, Paraguay, El Salvador e Chile."}]},{"chapter":{"index":3,"en":{"title":"The legends","text":"Football is built on top of the legendary stories that players have written on the green fields all around the world. Who is the greatest ever? <i>O Rei</i> <a href=\"#\" data=\"Pelé-Oct 23, 1940\" data-index=\"3\">Pelé</a>? <i>El pibe de oro</i> <a href=\"#\" data=\"Diego Maradona-Oct 30, 1960\" data-index=\"3\">Maradona</a>? The <i>Divin codino</i> <a href=\"#\" data=\"Roberto Baggio-Feb 18, 1967\" data-index=\"3\">Roberto Baggio</a>? Or <i>la pulga atomica</i> <a href=\"#\" data=\"Lionel Messi-Jun 24, 1987\" data-index=\"3\">Leo Messi</a> perhaps?"},"it":{"title":"Le leggende","text":"La storia del calcio &egrave; fatta dalle storie leggendarie dei suoi interpreti che hanno palleggiato sui campi di tutto il mondo.<br/>Chi &egrave; stato il pi&ugrave; grande di sempre? <i>O Rei</i> <a href=\"#\" data=\"Pelé-Oct 23, 1940\" data-index=\"3\">Pelé</a>? <i>El pibe de oro</i> <a href=\"#\" data=\"Diego Maradona-Oct 30, 1960\" data-index=\"3\">Maradona</a>? Il <i>Divin codino</i> <a href=\"#\" data=\"Roberto Baggio-Feb 18, 1967\" data-index=\"3\">Roberto Baggio</a>? O forse <i>la pulga atomica</i> <a href=\"#\" data=\"Lionel Messi-Jun 24, 1987\" data-index=\"3\">Leo Messi</a>?"}},"id":"Pelé-Oct 23, 1940","substories":[{"id":"Pelé-Oct 23, 1940","name":"O Rei Pelé","type":"short","descr":"<b>Pelé</b>, il Calciatore del Secolo per la FIFA, l'unico calciatore al mondo ad aver vinto tre edizioni del campionato mondiale di calcio. A passato 17 anni al Santos, dove ha segnato 571 reti in 586 partite. La FIFA gli riconosce il record di reti realizzate in carriera, 1281 in 1363 partite."},{"id":"Diego Maradona-Oct 30, 1960","name":"Diego Armando Maradona","type":"short","descr":"<p><i>D10S, El pibe de oro, la mano de Dios, Dieguito, Pelusa</i>... forse il pi&ugrave; grande di ogni tempo. Nella sua carriera ha fatto sognare generazioni di tifosi. Ha militato in sei squadre diverse.</p><p><i>“Tifosi di tutti i quartieri tradivano le loro squadre del cuore per vedere quel genio per il quale un fazzoletto di terreno era più di un latifondo. Alcuni lo confusero con Dio, e quando sei poco più che un bambino non hai motivo di mettere in dubbio l’opinione dei grandi.”</i><br/>(Jorge Valdano)</p>","shortDescr":"<p><i>D10S, El pibe de oro, la mano de Dios, Dieguito, Pelusa</i>... forse il pi&ugrave; grande di ogni tempo. Nella sua carriera ha fatto sognare generazioni di tifosi. Nella sua carriera ha militato in sei squadre diverse.</p>"}]}]

