import React, { Component } from "react";
import { extent, sum } from "d3-array";
import { scaleLinear } from "d3-scale";
import { format } from 'd3-format';
import SimpleAnnotation from "../SimpleAnnotation/SimpleAnnotation";
import "./histogram.css";

const WIDTH = 100;
const HEIGHT = 180;
const margins = {
  top: 0,
  right: 5,
  bottom: 0,
  left: 40
};

class Histogram extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { data, extents, max, shadow, annotation, labels } = this.props;
    if (!data) {
      return null;
    }
    // console.log("DATA", data);

    const xScale = scaleLinear()
      //.domain(extent([...data.map(d => d.x0), ...data.map(d => d.x1)]))
      .domain(extents.x)
      .range([0, WIDTH])
      .nice();

    const sumN = sum(data.map(d => d.n));

    const yScale = scaleLinear()
      .domain([0.001, max]) // ...data.map(d => d.n)]))
      .range([1, HEIGHT - (margins.bottom + margins.top)])
      .nice();

    let binWidth = 0;
    if(data.length > 0) {
      binWidth = xScale(data[0].x1) - xScale(data[0].x0);
    }

    // console.log('yScale', yScale.ticks())
    return (
      <div
        className={`histogram ${shadow ? 'shadow' : 'main'}`}
        style={{ width: `${WIDTH}%`, height: `${HEIGHT}px` }}
      >
        {annotation && <SimpleAnnotation {...annotation} left={`calc(${xScale(annotation.bin) + (annotation.orientation === 'right-to-left' ? binWidth : 0)}% + ${margins.left}px)`} top={`${HEIGHT - yScale(data[annotation.bin].n / sumN)}px`} width={`130px`} />}
        <div className="histogram-y-axis">
          <div className="ticks">
            {yScale.ticks(5).map((d,i) => {
              const y = yScale(max - d);
              return <span key={`tick${d}`} className="tick" style={{top:`${y + margins.top}px`}}>
                <span style={{width:`${margins.left - 10}px`}}>
                {`${format(',.0%')(d)}${i === 4 && labels ? ` ${labels.y}` : ''}`}
                </span>
              </span>;
            })}
          </div>
        </div>
        <div className="bars" style={{ marginLeft: `${margins.left}px`, width:`calc(100% - ${40}px)`}}>
          {data.map((d,i) => {
            const barStyle = {
              height: `${yScale(d.n / sumN)}px`
            };
            return (
              <div
                key={`bar${d.x1}-${d.x2}`}
                className="bar"
                style={{ width: `${binWidth}%` }}
              >
                <div style={barStyle}>
                  <span className="label">{d.x0}<h4>{!i && labels ? labels.x : ''}</h4></span>
                </div>
              </div>
            );
          })}
        </div>

      </div>
    );
  }
}

export default Histogram;



