import React, { Component } from "react";
import { extent } from "d3-array";
import Histogram from './Histogram';
import SimpleSlider from '../SimpleSlider/SimpleSlider';

import translations from "./../../translations.json";

import "./overlapped-distributions.css";

class Distributions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: 1,
    }
  }
  async componentDidMount() {
    const res = await fetch(
      `assets/transfers/data/${this.props.file}`
    );

    const distributions = await res.json();

    // console.log(distributions)
    this.setState({
      distributions,
    })
  }
  changeStatus(status) {
    this.setState({status});
  }
  render() {
    const {distributions, status} = this.state;
    const {groupMap, max, lang} = this.props;

    if(!distributions) {
      return null;
    }

    const values = { x: [], y: []};

    Object.values(distributions).forEach(distribution => {
      // console.log(distribution)
      values.x=[...values.x, ...distribution.map(d => d.x0), ...distribution.map(d => d.x1)];
      values.y=[...values.y, ...distribution.map(d => d.n)];
    })

    const extents = {
      x: extent(values.x),
      y: extent(values.y),
    };

    const annotations = [
      {
        lang,
        text: {
          en: `<h4>${groupMap.before.title}</h4>${groupMap.before.subtitle}</b>`,
          it: `<h4>${groupMap.before.title}</h4>${groupMap.before.subtitle}`,
        },
        bin: 5,
        orientation: "no-arrow right",
      },
      {
        lang,
        text: {
          en: `<h4>${groupMap.after.title}</h4>${groupMap.after.subtitle}`,
          it: `<h4>${groupMap.after.title}</h4>${groupMap.after.subtitle}`,
        },
        bin: 9,
        orientation: "no-arrow right",
      },
    ];

    const sliderLabels = {
      en: ['before', 'after'],
      it: ['prima', 'dopo'],
    }

    return <div className="distributions">
      <div className="charts-container">
      {Object.entries(distributions).filter(d => groupMap[d[0]]).map((d,i) => {
        return <div key={d[0]} className={`distribution ${status === i ? 'main' : 'shadow'}`}>
          <div className="histograms">
            <Histogram key={d[0]} data={d[1]} extents={extents} max={max} annotation={annotations[i]} labels={{y: translations[lang]['of players'], x: translations[lang]['number of transfers']}}/>
            <Histogram key={`${d[0]}-shadow`} data={d[1]} extents={extents} max={max} shadow annotation={annotations[i]}/>
          </div>
        </div>;
      })}
      <SimpleSlider callback={(status) => this.changeStatus(status)} labels={sliderLabels[lang]}/>
      </div>
    </div>
  }
}

export default Distributions;



