import React from 'react';

import './player-name.css';

const PlayerName = props => {
  const {player,isMobile} = props;
  if(!player) {
    return null;
  }
  const playerNameStyle = {};
  if(!isMobile && player.teamsForDots) {
    playerNameStyle.left = `${
      player.teamsForDots[
        player.teamsForDots.length - 1
      ].x - (player.id === 'Sebastián Abreu-Oct 17, 1976' ? 140 : 0)
    }px`;
    playerNameStyle.top = `${
          player.teamsForDots[
            player.teamsForDots.length - 1
          ].y + (player.id === 'Sebastián Abreu-Oct 17, 1976' ? 70 : 0)
        }px`;
  }
  return <div
    className="player-name-container"
    style={playerNameStyle}
  >
    <div className="player-name">
      <img
        src={`assets/transfers/img/square/${player.id
          .toLowerCase()
          .replace(/[\,\s]/gi, '_')}.png`}
        onError={e => {
          // console.log('ERROR', e);
          e.target.style.display = 'none';
        }}
      />
      <span>
        {player.name}
        <br />
        <i className="player-dob">{player.dob}</i>
      </span>
    </div>
  </div>
}

export default PlayerName;



