const translations = {
  it: {
    pausiert: 'Pausa',
    jugend: 'Giovani',
    vereinslos: 'Senza contratto',
    unbekannt: 'Sconosciuto',
    'Atlético-Aviación': 'Atlético Madrid',
  },
  en: {
    pausiert: 'Break',
    jugend: 'Youth',
    vereinslos: 'Without Club',
    unbekannt: 'Unkown',
    'Atlético-Aviación': 'Atlético Madrid',
  }
};

export const translateTeam = (team, lang = 'it') => {
  if (!team) {
    //console.log('team', team);
    return team;
  }


  for (let text in translations[lang]) {
    const regex = new RegExp(text, 'gi');
    team = team.replace(regex, translations[lang][text]);
  }
  const encodedName = team;
  try {
    team = decodeURI(team);
  } catch (e) {
    team = encodedName;
  }
  return team;
};



