import React, { Component } from "react";
import DOMPurify from "dompurify";
import "intersection-observer";
import { range } from "d3-array";
import PlayerName from "../PlayersDataViz/PlayerName";
import "./stories.css";
// import stories from './stories.json';

class Stories extends Component {
  story = 0;
  index = 984;
  componentDidMount() {
    // const playerDataViz = document.querySelector(".players-dataviz");

    this.storiesContainer.querySelectorAll("a").forEach(d => {
      // console.log('link',d)
      d.addEventListener("click", e => this.clickPlayer(e));
    });
    if (this.props.isMobile) {
      return;
    }
    const observer = new IntersectionObserver(
      entries => {
        entries.forEach(entry => {
          // console.log('TESTING ON START', entry.intersectionRatio, entry.boundingClientRect)
          if (this.props.isMobile) {
            return;
          }

          const data = entry.target.getAttribute("data");
          const chapter = entry.target.getAttribute("data-chapter");
          // console.log(data,'===',this.player,entry);
          if (
            data &&
            ((chapter === "preface" && entry.intersectionRatio >= 0) ||
              entry.intersectionRatio > 0.1) &&
            entry.boundingClientRect.top >= 0
          ) {
            if ((data === "search" && this.player !== 'search') || (data === "balondor" && this.player !== 'balondor')) {
              this.player = data;
              // if (entry.intersectionRatio) {
              if (this.props.callback) {
                this.props.callback();
              }
              // }
            } else if (data === "intro" && this.player !== 'intro') {
              this.player = data;
              if (this.props.callback) {
                this.props.callback({
                  value: data,
                  players: [],
                  chapter: "intro"
                });
              }
            } else {
              // if (entry.intersectionRatio < 0.01) {
              if (this.player !== data && entry.intersectionRatio > 0.1) {
                this.player = data;
                if (this.props.callback) {
                  this.props.callback({
                    label: data,
                    value: data,
                    chapter
                  });
                }
              } else {
                if (this.props.positionCallback) {
                  this.props.positionCallback(entry.intersectionRatio);
                }
              }
            }
          }
        });
      },
      {
        root: null,
        rootMargin: "0px",
        threshold: range(100).map(d => d / 100),
      }
    );
    this.storiesContainer
      .querySelectorAll(".story")
      .forEach(d => observer.observe(d));
  }
  clickPlayer(e) {
    e.preventDefault();
    const player = e.target.getAttribute("data");
    const chapter = e.target.getAttribute("data-chapter");
    const index = +e.target.getAttribute("data-index");
    // console.log(e.target);
    if (!player) {
      return;
    }
    // console.log(player);
    this.props.callback({
      label: player,
      value: player,
      chapter,
      index
    });
  }
  showNext() {
    this.props.callback(this.index);
    this.index += 1;
  }
  render() {
    const {
      stories,
      lang,
      // playerList,
      children,
      isMobile,
      players,
      radius,
      storyIDs,
      height
    } = this.props;
    const prefaceStory = stories[0];
    const prefaceChapterText = isMobile
      ? prefaceStory.chapter[lang].shortText || prefaceStory.chapter[lang].text
      : prefaceStory.chapter[lang].text;

    const prefaceId = storyIDs[0];
    const prefacePlayer = players.find(player => player.id === prefaceId);
    let prefacePlayerHeight = prefacePlayer
      ? prefacePlayer.teamsOriginal.length * (radius * 2 + 10 * 2)
      : 0;

    if (isMobile && prefaceId === "Kazuyoshi Miura-Feb 26, 1967") {
      prefacePlayerHeight = height * 1.4;
    }

    const prefaceStoryStyle =
      prefacePlayerHeight > height
        ? { height: `${prefacePlayerHeight}px` }
        : {};

    return (
      <div
        ref={el => (this.storiesContainer = el)}
        className="player-stories row"
      >
        <div className="col-xs-12 col-sm-6 col-sm-offset-2">
          <div
            key={prefaceStory.id}
            className="story"
            data={prefaceStory.id}
            data-chapter="preface"
            data-index={0}
            style={prefaceStoryStyle}
          >
            <div className="story-chapter">
              <h4>{lang==='en' ? 'Preface' : 'Prologo'}</h4>
              <h1>
                <span>{prefaceStory.chapter[lang].title}</span>
              </h1>
              <p
                dangerouslySetInnerHTML={{
                  __html: DOMPurify.sanitize(prefaceChapterText, {
                    ALLOWED_ATTR: ["data", "data-index"]
                  })
                }}
              />
            </div>
            {isMobile && (
              <PlayerName player={prefacePlayer} isMobile={isMobile} />
            )}
          </div>

          {!isMobile && (
            <div key="story-bosman" className="story" data="intro">
              {lang === "en" && (
                <div className="story-chapter">
                  <h4>Introduction</h4>
                  <h1>
                    <span>The Bosman ruling</span>
                  </h1>
                  <p>
                    We have analyzed the careers of more than 100,000 professional footballers.
                  </p>
                  <p>
                    1995 is the year when football changed.
                    The <i>Bosman ruling</i> removed a lot of the limitations on the free transfer of players in Europe. This increased the number of transfers a player would see in his career.<br/>
                    Around the time, the ruling facilitated big moves for <a href="#" data="Edgar Davids-Mar 13, 1973" data-chapter="bosman">Edgar Davids</a> (Ajax to Milan AC 1996), <a href="#" data="Brian Laudrup-Feb 22, 1969" data-chapter="bosman">Brian Laudrup</a>(Rangers to Chelsea, 1998) and <a href="#" data="Steve McManaman-Feb 11, 1972" data-chapter="bosman">Steve McManaman</a> (Liverpool to Real Madrid, 1999) among many others.
                  </p>
                  {/*
              <p>Sparkline Seria A stranieri: 1989-90: 53, 1999-00: 155, 2009-10: 228</p>
              <p>Sparkline Premier 1994/1995: 28.9%, 1998/99: 45.8%, 57.6: 2003/04, 63%: 2008</p>
            */}
                </div>
              )}
              {lang === "it" && (
                <div className="story-chapter">
                  <h4>Introduzione</h4>
                  <h1>
                    <span>La sentenza Bosman</span>
                  </h1>
                  <p>
                    Abbiamo analizzato le carriere di pi&ugrave; di
                    centomila giocatori professionisti.
                  </p>
                  <p>
                    Il 1995 &egrave; l'anno spartiacque per il calcio. Il 15
                    Dicembre fu adottata la sentenza Bosman.
                    Alcuni dei trasferimenti pi&ugrave; famosi che hanno usufruito della sentenza sono stati quelli di <a href="#" data="Edgar Davids-Mar 13, 1973" data-chapter="bosman">Edgar Davids</a> (dall'Ajax al Milan AC 1996), <a href="#" data="Brian Laudrup-Feb 22, 1969" data-chapter="bosman">Brian Laudrup</a>(dai Rangers al Chelsea, 1998) e <a href="#" data="Steve McManaman-Feb 11, 1972" data-chapter="bosman">Steve McManaman</a> (dal Liverpool al Real Madrid, 1999).
                  </p>
                  <p>
                    La sentenza impedì alle leghe europee di porre un tetto al numero di stranieri.
                    Questo favor&igrave; il trasferimento di giocatori.
                  </p>
                  {/*
              <p>Sparkline Seria A stranieri: 1989-90: 53, 1999-00: 155, 2009-10: 228</p>
              <p>Sparkline Premier 1994/1995: 28.9%, 1998/99: 45.8%, 57.6: 2003/04, 63%: 2008</p>
            */}
                </div>
              )}
            </div>
          )}
          {stories.filter((d, i) => i > 0).map((d, i) => {
            // const substories = d.substories || [];
            const chapterText = isMobile
              ? d.chapter[lang].shortText || d.chapter[lang].text
              : d.chapter[lang].text;
            const id = storyIDs[i + 1];
            const player = players.find(player => player.id === id);
            const playerHeight = player
              ? player.teamsOriginal.length * (radius * 2 + 10 * 2)
              : 0;
            const storyStyle =
              isMobile && playerHeight > height
                ? { height: `${playerHeight}px` }
                : {};
            return (
              <div
                key={d.id}
                className={`story ${d.type || "long"}`}
                data={d.id}
                style={storyStyle}
              >
                <div className="story-chapter">
                  <h4>{lang==='en'?'Chapter':'Capitolo'} {d.chapter.index}</h4>
                  {d.chapter[lang].title && (
                    <h1>
                      <span>{d.chapter[lang].title}</span>
                    </h1>
                  )}
                  {chapterText && (
                    <p
                      dangerouslySetInnerHTML={{
                        __html: DOMPurify.sanitize(chapterText, {
                          ALLOWED_ATTR: ["data", "data-index"]
                        })
                      }}
                    />
                  )}
                </div>
                {isMobile && <PlayerName player={player} isMobile={isMobile} />}
                {/*substories.filter(d => d.id === this.player).map(substory => {
                  return (
                    <div className="substory">
                      <h3>
                        {substory.name}
                      </h3>
                      {this.player === substory.id && (
                        <p
                          dangerouslySetInnerHTML={{
                            __html: DOMPurify.sanitize(substory.descr)
                          }}
                        />
                      )}
                    </div>
                  );
                })*/}
              </div>
            );
          })}
          {children}
        </div>
      </div>
    );
  }
}

export default Stories;



