import React, { Component } from 'react';
import DOMPurify from 'dompurify';

import './SimpleAnnotation.css'

class SimpleAnnotation extends Component {
  render() {
    const {text,left,width,top,lang='en',orientation = 'left-to-right'} = this.props;
    const annotationClass = `annotation ${orientation}`;
    const annotationStyle = {
      left,
      top,
    };
    const pStyle = {};
    if(width) {
      pStyle.width = width;
    }
    // console.log(this.props)
    return (
      <div className={annotationClass} style={annotationStyle}>
        <p className="text" dangerouslySetInnerHTML={{
          __html: DOMPurify.sanitize(text[lang])
        }}
        style={pStyle}
        />
        <div className="arrow">
          <span></span>
        </div>
      </div>
    );
  }
}

export default SimpleAnnotation;



